export default function AppRouter() {
    let routes = OrderedProjects.map((project, i, projects) => (
        <Route
            key={i}
            path={project.project.link}
            exact
            render={props =>
                <ProjectPage
                    config={project.project}
                    next={i == projects.length - 1 ? null : projects[i + 1].project}
                    pre={i == 0 ? null : projects[i - 1].project}
                />}
        />
    ))
    return (
        <Router>
            <Route
                render={({ location }) => (
                    <ScrollToTop>
                        <Page>
                            <TransitionGroup>
                                <CSSTransition
                                    appear={true}
                                    classNames="fade"
                                    key={location.key}
                                    timeout={1200}
                                >
                                    <Switch location={location}>
                                        <Route path="/" exact component={RootPage} />
                                        {routes}
                                    </Switch>
                                </CSSTransition>
                            </TransitionGroup>
                        </Page>
                    </ScrollToTop>
                )}
            />
        </Router>
    )
}