class Projects extends React.Component {
    constructor(props) {
        super(props);
        this.handleResize = this.handleResize.bind(this);
        this.handleScroll = this.handleScroll.bind(this);
        this.eleRef = React.createRef();
        this.isSmallScreen = false;
        this.cardStates = OrderedProjects.map(() => (
            false
        ));
        this.state = {
            cardStates: this.cardStates
        }
    }

    handleResize() {
        if (window.innerWidth > 480 && this.isSmallScreen) {
            this.isSmallScreen = false;
            document.removeEventListener("scroll", this.handleScroll);
        }
        else if (window.innerWidth <= 480 && !this.isSmallScreen) {
            this.isSmallScreen = true;
            document.addEventListener("scroll", this.handleScroll);
        }
    }

    handleScroll() {
        const { top, height } = this.eleRef.current.getBoundingClientRect();
        const cardHeight = height / (OrderedProjects.length + 1);
        const viewportHeight = window.innerHeight;
        const toBottom = viewportHeight - top;
        // is any card within the viewport?
        if (toBottom > 0 && -top < height - cardHeight) {
            let n = parseInt(toBottom / cardHeight);
            let cardStates = this.state.cardStates;
            n = n > OrderedProjects.length - 1 ? OrderedProjects.length - 1 : n;
            for (n; n >= 0; n--) {
                // get the distance between the top edge of this card and the bottom of viewport
                const offset = toBottom - n * cardHeight;
                if (offset >= viewportHeight + cardHeight)
                    break;
                else {
                    cardStates[n] = offset >= cardHeight * .75 && offset - viewportHeight <= cardHeight * .5 - 56;
                }
            }
            // console.log(this.state.cardStates);
            this.setState({ cardStates: cardStates });
        }
    }

    componentWillMount() {
        if (window.innerWidth <= 480) {
            this.isSmallScreen = true;
            document.addEventListener("scroll", this.handleScroll);
        }
        window.addEventListener("resize", this.handleResize);
    }

    componentWillUnmount() {
        document.removeEventListener("scroll", this.handleScroll);
        window.removeEventListener("resize", this.handleResize);
    }
    render() {
        const projectCards = OrderedProjects.map((project, i) => (
            <ProjectCard key={i} config={project.project} col={project.col} isFocused={this.state.cardStates[i]} />
        ));
        return (
            <div css={style} ref={this.eleRef}>
                {projectCards}
                <MoreStories />
            </div>
        )
    }
}

export default Projects;

const style = {
    label: 'Projects',
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
}